<template>
    <article>

        <table data-v-regInput>
            <tr>
                <td>Login ID</td>
                <td><input type="text" name="id" placeholder="半角英数字記号 必須" data-v-regInput></td>
            </tr>
            <tr>
                <td>Email</td>
                <td><input type="email" name="email" placeholder="半角英数字記号 必須" data-v-regInput></td>
            </tr>
            <tr>
                <td>パスワード</td>
                <td><input type="password" name="pw" placeholder="半角英数字記号 必須" data-v-regInput></td>
            </tr>
            <tr>
                <td>パスワード(再確認)</td>
                <td><input type="password" name="pw-re" placeholder="半角英数字記号 必須" data-v-regInput></td>
            </tr>
        </table>
        <div class="info" data-v-regInput>
        <p>Login IDは、プロフィール名となります。<br>他のユーザーからわかりやすい名前の設定をおすすめします（登録後、変更可能）。</p>
        <p>Emailの公開・非公開設定は、プロフィール編集画面から変更が可能です（デフォルト：非公開）。</p>
        </div>

        <div class="buttonBase" v-on:click="toRegister">登録</div>

    </article>
</template>

<script>
export default {
    name : "RegInput",

    methods:{
        toRegister(){
            // ToDo call Register action
            this.$router.push({name:'reg-comp'})
        }
    }
}
</script>


<style>
    table[data-v-regInput]{
        width: 100%;
        margin: 0 auto;
        /* margin-bottom: 30px; */
    }

    .info[data-v-regInput]{
        margin: 30px 0px;
        font-size: 80%;
    }
</style>



