import 'babel-polyfill'
import Vue  from 'vue'
import Vuex from 'vuex'
import router from '@/router'

import Cookies  from 'js-cookie'
import axios    from 'axios'

Vue.use(Vuex)

const store = new Vuex.Store(
    {
        state: {
            appTitle : "@TJC",

            // トグルボタン表示可否
            showToggre : true,

            //  ログイン状態
            logined : false,

            // モーダルメッセージ表示可否
            showModal : false,
            modalMsg : "",

            // ローディング
            loading : false,
            
            // サイドバー表示制御
            showSideBar : false,

            // ユーザー情報
            userInfo : {
                userId  : "shunya"
            },

            // プロフィール
            profile : {},

            // SinglePosMap
            posSingleMap : {
                lat: -25.363,
                lng: 131.044
            }
        },

        getters :{
            appTitle(state)
            {
                return state.appTitle
            },

            showToggre(state)
            {
                return state.showToggre
            },

            logined(state)
            {
                return state.logined
            },

            showModal(state)
            {
                return state.showModal
            },

            modalMsg(state)
            {
                return state.modalMsg
            },

            loading(state)
            {
                return state.loading
            },

            showSideBar(state)
            {
                return state.showSideBar
            },

            userInfo(state)
            {
                return state.userInfo
            },

            profile(state)
            {
                return state.profile
            },

            posSingleMap(state)
            {
                return state.posSingleMap
            }
        },

        mutations: {
            setToggreState(state, payload)
            {
                state.showToggre = payload.toggreState
            },

            setLogined(state, payload)
            {
                state.logined = payload.logined
            },

            setShowModal(state, payload)
            {
                state.showModal = payload.modalState
            },
            setModalMsg(state, payload)
            {
                state.modalMsg = payload.modalMsg
            },

            setLoading(state, payload)
            {
                state.loading = payload.loadingState
            },

            setShowSidebar(state, payload)
            {
                state.showSideBar = payload.sidebarState
            },

            setProfile(state, payload)
            {
                state.profile = payload.profile
            },

            setPosSingleMap(state, payload)
            {
                state.posSingleMap = payload.pos
            },

            setResidence(state, payload)
            {
                state.profile.residences[payload.index].name = payload.name
                state.profile.residences[payload.index].lat  = payload.lat
                state.profile.residences[payload.index].lng  = payload.lng
            },

            addResidence(state, payload)
            {
                var reg = {
                    name : payload.name,
                    lat : payload.lat,
                    lng : payload.lng,
                    from : payload.from,
                    to : payload.to
                }
                state.profile.residences.push(reg)
            }
        },

        actions: {
            setToggreState({commit}, toggreState)
            {
                commit('setToggreState', { toggreState })
            },

            setShowModal({commit}, modalState)
            {
                commit('setShowModal', {modalState})
            },
            setModalMsg({commit}, modalMsg)
            {
                commit('setModalMsg', {modalMsg})
            },

            setLoading({commit}, loadingState)
            {
                commit('setLoading', {loadingState})
            },

            setShowSidebar({commit}, sidebarState)
            {
                commit('setShowSidebar', {sidebarState})
            },

            setPosSingleMap({commit}, pos)
            {
                commit('setPosSingleMap', {pos})
            },

            addResidence({commit}, pos)
            {
                commit('addResidence', {pos})
            },

            //----------------------------------------
            //  ログイン処理
            //----------------------------------------
            callLoginApi({commit}, loginInput)
            {
                // loadingモード
                commit('setLoading', {loadingState:true})

                const self = this
                
                axios.post(process.env.API_ENDPOINT + process.env.API_APPID + "auth", 
                    {
                        userId : loginInput.id,
                        password : loginInput.pw
                    }
                ) 
                .then(function(response){
                    commit('setLoading', {loadingState:false})
                    // login success
                    if(response.status == 200)
                    {
                        commit('setLogined', {logined:true})
                        commit('setToggreState', {toggreState:true})
                        Cookies.set('token', response.data.token, { expires: process.env.TokenExpires })
                        router.push('/')
                    }
                    else{
                        commit('setShowModal', {modalState:true})
                        commit('setModalMsg', {modalMsg:"Login ID、パスワードが正しくありません。"})
                    }
                })
                .catch(function(error){
                    commit('setLoading', {loadingState:false})
                    commit('setShowModal', {modalState:true})
                    commit('setModalMsg', {modalMsg:"@TJCサービスから応答がありません。<br>時間をおいてから再度お試しください。"})
                })
            },

            //----------------------------------------
            //  ログインチェック
            //----------------------------------------
            async checkLogin({commit})
            {
                let token = Cookies.get('token')
                if(token == null || token.length == 0)
                {
                    console.log('info:not token')
                    return
                }

                await axios.get(process.env.API_ENDPOINT + process.env.API_APPID + "user",
                    {
                        headers :
                        {
                            'Authorization' : token
                        }
                    }
                )
                .then(function(response){
                    commit('setLoading', {loadingState:false})
                    // login success
                    if(response.status == 200)
                    {
                        commit('setLogined', {logined:true})
                        console.log('info:login ok')
                    }
                })
                .catch(function(error){})
            },

            //----------------------------------------
            //  ログアウト処理
            //----------------------------------------
            callLogoutApi({commit})
            {
                // loadingモード
                commit('setLoading', {loadingState:true})

                let token = Cookies.get('token')

                axios.get(process.env.API_ENDPOINT + process.env.API_APPID + "user/logout", 
                    {
                        headers :
                        {
                            'Authorization' : token
                        }
                    }
                )
                .then(function(response){
                    commit('setLoading', {loadingState:false})
                    Cookies.remove('token')
                    location.reload()
                })
                .catch(function(error){
                    commit('setLoading', {loadingState:false})
                    Cookies.remove('token')
                    location.reload()
                })
            },

            //----------------------------------------
            // パスワード変更
            //----------------------------------------
            updatePW({commit}, pw)
            {
                // ToDo パスワード変更API call
                commit('setShowModal', {modalState:true})
                commit('setModalMsg', {modalMsg:"パスワードを変更しました。<br>再度 ログインしなおしてください。"})
                Cookies.remove('token')
            },

            //----------------------------------------
            //  マイページ初期化
            //----------------------------------------
            initMypage({commit})
            {
                // loadingモード
                commit('setLoading', {loadingState:true})

                let token = Cookies.get('token')

                // ToDo ここでProfile取得APIを呼ぶ
                axios.get(process.env.API_ENDPOINT + process.env.API_APPID + "user",
                    {
                        headers :
                        {
                            'Authorization' : token
                        }
                    }
                )
                .then(function(response){
                    commit('setLoading', {loadingState:false})
                    // login success
                    if(response.status == 200)
                    {
                        var profileData = {
                            myProfile : true,
                            userId  : "xxxx",
                            contacts : [
                                {
                                    type : "Email",
                                    value : "test.xxxx@example.com",
                                    url : null
                                },
                                {
                                    type : "twitter",
                                    value : "@xxxx",
                                    url : "https://twitter.com/kou_jich"
                                },
                                {
                                    type : "Facebook",
                                    value : "https://www.facebook.com/xxxx",
                                    url : "https://www.facebook.com/"
                                }
                            ],
                            dob : "1990-04-01",
                            residences : [
                                {
                                    name : "明大前",
                                    lat : 35.668349,
                                    lng : 139.650554,
                                    from : "2015-03-28",
                                    to : null
                                },
                                {
                                    name : "穂積",
                                    lat : 35.396082,
                                    lng : 136.693097,
                                    from : "1990-10-25",
                                    to : "2015-03-28"
                                }
                            ],
                            univ :{
                                from : "2009-04-07",
                                to : "2015-03-25",
                                dep : [
                                    "工学部xxx学科",
                                    "工学研究科xxx専攻 xxx研究室"
                                ]
                            },
                            comment : "都内でITエンジニアなう"
                        }
        
                        commit('setProfile', {profile:profileData})


                    }
                    else{
                        // commit('setShowModal', {modalState:true})
                        // commit('setModalMsg', {modalMsg:"Login ID、パスワードが正しくありません。"})
                    }
                })
                .catch(function(error){
                    // commit('setLoading', {loadingState:false})
                    // commit('setShowModal', {modalState:true})
                    // commit('setModalMsg', {modalMsg:"@TJCサービスから応答がありません。<br>時間をおいてから再度お試しください。"})
                })
            },

            //----------------------------------------
            //  プロフィール更新
            //----------------------------------------
            updateProfile({commit}, inputProfile)
            {
                // ToDo プロフィール更新API call
                commit('setShowModal', {modalState:true})
                commit('setModalMsg', {modalMsg:"マイプロフィールを更新しました。"})
            },

            //----------------------------------------
            //  居住地変更
            //----------------------------------------
            async updateResidence({commit}, input)
            {
                commit('setLoading', {loadingState:true})

                let self = this

                await axios.get("https://maps.googleapis.com/maps/api/geocode/json?address=" + input.name + "&key=" + process.env.MAP_API_KEY)
                .then(function(response){

                    commit('setLoading', {loadingState:false})

                    if(response.status == 200 && response.data.status == "OK")
                    {
                        var posInput = {
                            index : input.index,
                            name  : input.name,
                            lat   : response.data.results[0].geometry.location.lat,
                            lng   : response.data.results[0].geometry.location.lng
                        }
                        if(input.save)
                        {
                            commit('setResidence', posInput)
                        }

                        commit('setPosSingleMap',{pos:{lat:response.data.results[0].geometry.location.lat, lng:response.data.results[0].geometry.location.lng}})
                    }
                    else
                    {
                        console.log("API呼出エラー")
                        commit('setShowModal', {modalState:true})
                        commit('setModalMsg', {modalMsg:'"' + input.name + '" が見つかりません。'})
                    }

                })
                .catch(function(error){
                    console.log("API exception" + error)
                    commit('setShowModal', {modalState:true})
                    commit('setModalMsg', {modalMsg:'"' + input.name + '" が見つかりません。'})
                })
            }
        }
    }
)
export default store


